<template>
  <el-menu id="song-list-menu" active-text-color="black" :default-active="activeIndex" mode="horizontal">
    <el-menu-item index="1" @click="changeActiveIndex('1')">歌曲列表</el-menu-item>
    <el-menu-item index="2" @click="changeActiveIndex('2')">评论({{ commentCount }})</el-menu-item>
  </el-menu>
  <el-table v-show="activeIndex==='1'"
            v-loading="!songsDetail.songs"
            :data="songsDetail.songs"
            :stripe="true"
            style="width: 100%"
            @cell-dblclick="playSong"
            :row-style="rowStyle">
    <el-table-column label="" width="50px">
      <template #default="scope">
        {{ scope.$index }}
      </template>
    </el-table-column>
    <el-table-column label="" width="30px">
      <template #default="scope">
        <el-tooltip effect="light" content="添加到播放列表" placement="top" :open-delay="500">
          <i class="iconfont icon-tianjia" @click="addToPlayList(scope.row)"></i>
        </el-tooltip>
      </template>
    </el-table-column>
    <el-table-column prop="name" label="标题"/>
    <el-table-column prop="ar" label="歌手">
      <template #default="scope">
        <span v-for="(item,index) in scope.row.ar" :key="item.id">
          {{ index == 0 ? item.name : '/' + item.name }}
        </span>
      </template>
    </el-table-column>
    <el-table-column prop="al.name" label="专辑"/>
    <el-table-column prop="dt" label="时间">
      <template #default="scope">
        {{ formatSongTime(scope.row.dt) }}
      </template>
    </el-table-column>
  </el-table>
  <div v-show="activeIndex==='2'">comments</div>
</template>

<script>
import {ref, toRaw, computed} from 'vue'
import {useStore} from 'vuex'
import {ElMessage} from 'element-plus'

export default {
  name: "SongListMenu",
  props: ['commentCount', 'songsDetail'],
  setup() {
    const store = useStore();
    // 菜单选中歌曲列表 或 评论
    let activeIndex = ref('1')

    function changeActiveIndex(i) {
      activeIndex.value = i
      // console.log(activeIndex)
    }

    // 表格
    function formatSongTime(n) {
      n = Math.floor(n / 1000)
      let min = (n / 60) >= 10 ? '' : '0' + Math.floor(n / 60);
      let sec = (n % 60 >= 10 ? '' : '0') + (n % 60);
      return min + ':' + sec
    }

    // 添加到播放列表
    function addToPlayList(songDetail) {
      store.commit('ADD_TO_PLAYLIST', songDetail)
      ElMessage({
        message: '已添加到播放列表中',
        type: 'success',
      })
    }

    // 播放音乐
    let currentSongDetail = computed(() => store.state.currentSongDetail)

    function playSong(row) {
      if (currentSongDetail.value != null && row.id == currentSongDetail.value.id) return;

      localStorage.setItem('currentSongDetail', JSON.stringify(row))
      store.commit('DBCLICK_SONG', toRaw(row))

    }

    function rowStyle({row}) {
      if (!currentSongDetail.value) {
        return;
      }
      if (row.id
          ===
          currentSongDetail.value.id) {
        return 'color:red;'
      }
      return ''
    }

    return {
      activeIndex,
      changeActiveIndex,
      formatSongTime,
      rowStyle,
      playSong,
      currentSongDetail,
      addToPlayList
    }
  },

}
</script>

<style scoped>
i.iconfont {
  font-size: 20px;
  width: 20px;
  margin: 2px auto
}

i.iconfont:hover {
  color: red;
}


.el-table tbody tr .iconfont {
  opacity: 0;
  transition: opacity 0.5s;
}

.el-table tbody tr:hover .iconfont {
  opacity: 1;
}

/* 点击出来的下划线进行修改 */

#song-list-menu .el-menu-item.is-active {
  border-color: #ec4141;
  font-size: 16px;
  font-weight: 900;
}

/* 整体的下划线进行隐藏 */

.el-menu.el-menu--horizontal {
  border-bottom: none;
}

</style>