<template>
  <div class="song-list">
  <SongListDetail :songListDetail="songListDetail" :songsDetail="songsDetail"></SongListDetail>
  <SongListMenu comment-count="xxx" :songsDetail="songsDetail"></SongListMenu>
  </div>
</template>

<script>
import {useRouter} from 'vue-router'
import {onMounted, ref,h} from 'vue'
import axios from 'axios'
import SongListDetail from "@/components/home/songList/SongListDetail";
import SongListMenu from "@/components/home/songList/SongListMenu";
import {ElMessage} from "element-plus";

export default {
  name: "SongList",
  components: {SongListMenu, SongListDetail},
  setup() {
    onMounted(()=>{
      ElMessage({
        message: h('div', null, [
          h('span', null, '点击'),
          h('b', null, '红色播放按钮'),
          h('span', null, '或者双击'),
          h('b', null, '某首歌曲'),
          h('span', null, '进行播放，播放歌曲后'),
          h('b', null, '点击左下角歌曲封面，显示滚动歌词'),
        ]),
        type: 'success',
        duration: 0,
        showClose: true
      })
    })


    // 获取route参数
    const params = useRouter().currentRoute.value.params
    // 歌单信息
    let songListDetail = ref({})
    // 每首歌曲的详情
    let songsDetail = ref({})

    axios.get("playlist/detail", {
      params: {
        id: params.id,
      },
    }).then((res) => {
      // 获取歌单的信息
      songListDetail.value = res.data.playlist;
      let queryIds = ''
      songListDetail.value.trackIds.forEach((item) => {
        queryIds += item.id + ','
      })
      return axios.get("song/detail", {
        params: {
          ids: queryIds.substr(0, queryIds.length - 1),
        }
      })
    }).then((res) => {
      // 获取歌曲的信息
      songsDetail.value = res.data
      // console.log(songsDetail)
    })

    return {
      songListDetail, songsDetail
    }
  }
}
</script>

<style scoped>
.song-list{
  margin-bottom: 200px;
}
</style>