<template>
  <div v-if="songListDetail && songListDetail.creator"
       class="song-list-detail-top-container clearfix">
    <!-- 歌单封面 -->
    <el-image class="song-list-cover-img" :src="songListDetail.coverImgUrl" fit="fill"
    ></el-image>
    <div class="song-list-info">
      <!--标题-->
      <div class="song-list-info-title">
        <div style="
          color: #d03535;
          border: 1px solid #d03535;
          display: inline-block;
          font-size: 20px;
          margin-right: 10px;
          border-radius: 5px;
          padding: 0 7px;
          cursor: default;
        ">
          歌单
        </div>
        {{ songListDetail.name }}
      </div>
      <!-- 创建者信息-->
      <div class="song-list-info-creator">
        <!-- 作者头像 -->
        <el-image
            :src="songListDetail.creator.avatarUrl"
            fit="fill"
            style="border-radius: 40px; height: 30px; width: 30px; vertical-align: middle"
        ></el-image>
        <!-- 作者名 -->
        <span style="color: #507daf; cursor: pointer; margin: 0 10px">
        {{ songListDetail.creator.nickname }}
      </span>
        <!-- 创建时间 -->
        <span style="font-size: 10px; margin-left: 5px">
        {{ toTime(songListDetail.createTime) }} 创建
      </span>
      </div>
      <!--播放按钮-->
      <div class="song-list-play-btn clearfix">
        <el-tooltip effect="light" content="将播放列表替换为该歌单" placement="top" :open-delay="500">
          <div class="play-all" @click="playSongList">

            <i class="iconfont icon-24gl-play">播放全部</i>

          </div>
        </el-tooltip>
        <el-tooltip effect="light" content="将歌单添加到播放列表" placement="top" :open-delay="500">
          <div class="add-all" @click="addSongListToPlayList">
            <i class="iconfont icon-tianjia"></i>
          </div>
        </el-tooltip>
      </div>
      <!--标签-->
      <div class="song-list-tag" style="margin-top: 10px; font-size: 12px">
        标签：
        <span v-for="(item,index) in songListDetail.tags" :key="index">
          {{ index == 0 ? item : '/' + item }}
        </span>
      </div>
      <div>...</div>
      <div>...</div>
      <div>...</div>

    </div>
  </div>
</template>

<script>
import {computed} from 'vue'
import {ElMessage, ElMessageBox} from 'element-plus'
import {useStore} from 'vuex'

export default {
  name: "SongListDetail",
  props: ['songListDetail', 'songsDetail'],
  setup(props) {
    const store = useStore()

    function toTime(num) {
      return new Date(num).toISOString().split('T')[0]
    }

    function playSongList() {
      ElMessageBox.confirm('确认把播放列表替换成该歌单吗？', '', {
            confirmButtonText: '是的',
            cancelButtonText: '取消',
            type: 'warning',
          }
      ).then(() => {
        store.commit('PLAY_SONGLIST', props.songsDetail.songs)
        ElMessage({
          type: 'success',
          message: '播放列表已替换成该歌单',
        })
      }).catch(() => {
        ElMessage({
          type: 'info',
          message: '取消替换',
        })
      })


    }

    function addSongListToPlayList() {
      store.commit('ADD_SONGLIST_TO_PLAYLIST',props.songsDetail.songs)
      ElMessage({
        type: 'success',
        message: '歌单已添加到播放列表',
      })
    }

    return {
      toTime,
      playSongList,
      addSongListToPlayList
    }
  },

}
</script>

<style lang="less" scoped>
.song-list-detail-top-container {
}

.clearfix::after {
  content: '';
  display: block;
  height: 0;
  clear: both;
}

.song-list-cover-img {
  float: left;
  width: 25%;
  border-radius: 10px;
}

.song-list-info {
  float: left;
  margin-left: 5%;
  width: 70%;
}


.song-list-info-title {
  font-size: 25px;
  font-weight: bolder;
}

.song-list-info-creator {
  margin-top: 10px;
  font-size: 12px;
}

.song-list-play-btn {
  margin-top: 10px;
  color: white;
  font-size: 20px;
  line-height: 36px;

  .play-all {
    width: 100px;
    height: 36px;
    padding-left: 20px;
    float: left;
    background-color: red;
    border-radius: 50px 0 0 50px;
  }

  .add-all {
    width: 30px;
    height: 36px;
    float: left;
    background-color: red;
    border-radius: 0 50px 50px 0;
  }
}

.song-list-play-btn:hover {
  .play-all {
    background-color: #e30e0e;
  }

  .add-all {
    background-color: #e30e0e;
  }

  .play-all:hover {
    background-color: #cb0505;
  }

  .add-all:hover {
    background-color: #cb0505;
  }
}


</style>